.input {
  border-radius: 0.5rem;
  outline: none;
  border: 1px solid rgb(189, 189, 189);
  width: 3rem;
  text-align: center;
}

.large {
  width: 20rem;
}

.left {
  text-align: left;
  padding-left: 1rem;
  font: inherit;
}

.invalid {
  border: 1px solid red;
  background-color: rgb(255, 231, 231);
}
